import axios from "axios";
import { useQuery } from "react-query";
import { ColumnChart } from "react-chartkick";
import "chartkick/chart.js";
import { useState } from "react";

export default function App() {
  let { data } = useQuery("customersData", getCustomersData);
  const [nameInputValue, setNameInputValue] = useState("");
  const [amountInputValue, setAmountInputValue] = useState();

  async function getCustomersData() {
    return await axios.get(`http://localhost:3000/Data`);
  }

  // console.log(data);

  function displayData(info) {
    let customersData = "";
    for (let i = 0; i < info.customers.length; i++) {
      for (let j = 0; j < info.transactions.length; j++) {
        if (info.customers[i].id === info.transactions[j].customer_id) {
          customersData += `<tr>
          <td>${info.customers[i].id}</td>
          <td>${info.customers[i].name}</td>
          <td>${info.transactions[j].id}</td>
          <td>${info.transactions[j].amount}</td>
          <td>${info.transactions[j].date}</td>
          </tr>`;
        }
      }
    }
    document.getElementById("tableBody").innerHTML = customersData;
  }

  if (data) {
    displayData(data.data);
  }

  function getChartData(info) {
    let chartDates = [];
    let chartAmounts = [];
    let amount = 0;
    let chartData = [];
    for (let i = 0; i < info.transactions.length; i++) {
      chartDates.push(info.transactions[i].date);
    }
    let filteredChartDates = chartDates.filter(
      (value, index) => chartDates.indexOf(value) === index
    );

    for (let i = 0; i < filteredChartDates.length; i++) {
      for (let j = 0; j < info.transactions.length; j++) {
        if (info.transactions[j].date === filteredChartDates[i]) {
          amount += info.transactions[j].amount;
        }
      }
      chartAmounts.push(amount);
      chartData.push([filteredChartDates[i], amount]);
      amount = 0;
    }
    // console.log(filteredChartDates);
    // console.log(chartAmounts);
    // console.log(chartData);
    return chartData;
  }

  function searchByName(term) {
    let foundedItems = [];
    for (let i = 0; i < data.data.customers.length; i++) {
      if (
        data.data.customers[i].name
          .toLowerCase()
          .includes(term.toLowerCase()) === true
      ) {
        foundedItems.push(data.data.customers[i]);
      }
    }

    if (foundedItems.length !== 0) {
      let customersData = "";
      for (let i = 0; i < foundedItems.length; i++) {
        for (let j = 0; j < data.data.transactions.length; j++) {
          if (foundedItems[i].id === data.data.transactions[j].customer_id) {
            customersData += `<tr>
            <td>${foundedItems[i].id}</td>
            <td>${foundedItems[i].name}</td>
            <td>${data.data.transactions[j].id}</td>
            <td>${data.data.transactions[j].amount}</td>
            <td>${data.data.transactions[j].date}</td>
            </tr>`;
          }
        }
      }
      document.getElementById("tableBody").innerHTML = customersData;
    }
  }

  function searchByAmount(amount) {
    let foundedItems = [];
    for (let i = 0; i < data.data.transactions.length; i++) {
      if (data.data.transactions[i].amount == amount) {
        foundedItems.push(data.data.transactions[i]);
      }
    }

    if (foundedItems.length !== 0) {
      let customersData = "";
      for (let i = 0; i < foundedItems.length; i++) {
        for (let j = 0; j < data.data.customers.length; j++) {
          if (foundedItems[i].customer_id === data.data.customers[j].id) {
            customersData += `<tr>
            <td>${data.data.customers[j].id}</td>
            <td>${data.data.customers[j].name}</td>
            <td>${foundedItems[i].id}</td>
            <td>${foundedItems[i].amount}</td>
            <td>${foundedItems[i].date}</td>
            </tr>`;
          }
        }
      }
      document.getElementById("tableBody").innerHTML = customersData;
    }
  }

  const handleChange = (event) => {
    setNameInputValue(event.target.value);
  };

  const handleChange2 = (event) => {
    setAmountInputValue(event.target.value);
  };

  return (
    <div>
      <main className="container">
        <div className="w-75 mx-auto pt-5 text-white mb-5">
          <h1 className="mb-5 fw-bolder text-center text-decoration-underline">
            Transactions Tracker
          </h1>
          <div className="mb-5">
            <div className="mb-3">
              <label className="form-label mb-2 fw-bold" htmlFor="searchByName">
                Search By Name...
              </label>
              <input
                type="text"
                id="searchByName"
                className="form-control"
                value={nameInputValue}
                onChange={handleChange}
              />
            </div>
            <div className="text-end">
              {data ? (
                <button
                  className="btn btn-success"
                  onInput={searchByName(nameInputValue)}
                >
                  Search
                </button>
              ) : (
                <button className="btn btn-success">Search</button>
              )}
            </div>
          </div>

          <div className="mb-5">
            <div className="mb-4">
              <label
                className="form-label mb-2 fw-bold"
                htmlFor="searchByAmount"
              >
                Search By Amount...
              </label>
              <input
                type="number"
                id="searchByAmount"
                className="form-control"
                value={amountInputValue}
                onChange={handleChange2}
              />
            </div>
            <div className="text-end">
              {data ? (
                <button
                  className="btn btn-success"
                  onInput={searchByAmount(amountInputValue)}
                >
                  Search
                </button>
              ) : (
                <button className="btn btn-success">Search</button>
              )}
            </div>
          </div>
        </div>

        <div className="mb-5">
          {data ? (
            <ColumnChart data={getChartData(data.data)} />
          ) : (
            <h2 className="text-white text-center">Loading</h2>
          )}
        </div>

        <table className="table w-75 mx-auto text-center">
          <thead className="text-white fs-5">
            <th>Customer ID</th>
            <th>Customer Name</th>
            <th>Transaction ID</th>
            <th>Amount</th>
            <th>Date</th>
          </thead>
          <tbody className="bg-white text-black" id="tableBody"></tbody>
        </table>
      </main>
    </div>
  );
}
